<template>
    <!--   -->
    <div ref="myDiv" class="radar-echart" />
</template>
<script>
// 
// var echarts = require('echarts')
var echarts = require('echarts/lib/echarts') // echarts
require('echarts/lib/chart/radar') // 
// 
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
import { LegendComponent } from 'echarts/components';
echarts.use([LegendComponent]);

export default {
    // 
    props:{
        postInfo:{
            type:Array,
            default:() => []
        },
        posts:{
            type:Array,
            default:()=>[]
        }
    },
    mounted() {
        const that = this
        const myChart = echarts.init(this.$refs.myDiv) // 
        myChart.setOption({
            title: {
                text: ''
            },
            tooltip: {},
            radar: {
                // shape: 'circle',
                name: {
                    textStyle: {
                        color: '#fff',
                        backgroundColor: '#999',
                        borderRadius: 3,
                        padding: [3, 5]
                    }
                },
                // 
                indicator: [
                    ...that.postInfo
                ]
            },
            series: [{
                type: 'radar',
                // areaStyle: {normal: {}},
                data: [
                    {
                        value: [
                            this.posts.filter(item=>item.name==='React').length, 
                            this.posts.filter(item=>item.name==='Vue').length, 
                            this.posts.filter(item=>item.name==='').length, 
                            this.posts.filter(item=>item.name==='').length, 
                            this.posts.filter(item=>item.name==='').length, 
                            this.posts.filter(item=>item.name==='').length, 
                            
                        ],
                        name: ''
                    }
                ]
            }]
        })
    }
}
</script>

<style>
.radar-echart {
    width: 43vw;
    height: 50vh;
}
</style>